<template>
<div>


  <!-- Search-bar -->
  <search/>
  <section class="section">
    <div class="container">
      <!-- User Card -->
      <app-home-loader v-if="isLoader"></app-home-loader>
      <userCard v-else :users="users" :showUsers="showUsers" />
    </div>
  </section>
</div>
</template>

<script>
import userCard from "../components/user-card";
import search from "../components/search";
import { mapGetters } from 'vuex'
import appHomeLoader from '../components/skeletonLoader/Home'

export default {
  name: 'home',
  components: {
    userCard,
    search,
    appHomeLoader
  },
  data: () => ({
    isLoader: false,
    users: [],
  }),
  computed: {
    ...mapGetters([
      'auth'
    ]),
    showUsers () {
      if(this.$route.query.search) {
        return this.users.filter(user=>{
          return user.fullnameEng.toLowerCase().match(this.$route.query.search.toLowerCase())
                  && user.qt5
        })
      }
      if(this.$route.query.batch && this.$route.query.university) {
        return this.users.filter(user=>{
          return  user.batch === this.$route.query.batch
                   &&
                  user.university === this.$route.query.university
                  && user.qt5
        })
      }
      if(this.$route.query.batch) {
        return this.users.filter(user=>{
          return user.batch === this.$route.query.batch
                  && user.qt5
        })
      }
      if(this.$route.query.university) {
        return this.users.filter(user=>{
          return  user.university === this.$route.query.university
                  && user.qt5
        })
      }
      else {
        return this.users.filter(user=>{
          return user
          && user.qt5
        })
      }
    },
  },
  methods: {
    async fetchUsers () {
      this.isLoader = true
      const res = await this.$axios.get('user/get-all')
      this.users = res.data.users
      setTimeout(()=>{
        this.isLoader = false
      }, 200)
    },
  },
  created () {
    this.fetchUsers()
  }
}
</script>
<style lang="scss" scoped>

.section{
  @include mobile{
    padding: 20px;
  }
}


</style>
