<template>
<div class="columns is-mobile is-multiline">
  <div class="column is-3-desktop is-4-tablet is-12-mobile" v-for="user in showUsers" :key="user.index" >
      <div class="user-card">
        <div class="user-card-header">
          <span class="batch">
            {{ user.batch }}
          </span>
        </div>
        <div class="user-card-image"
        :style="user.image ? { 'background-image': 'url(' + $server + user.image + ')' } : { 'background-image':
        'url(' +
        $profile + ')' }"
        ></div>
        <div class="user-card-content">
            <h3 class="name">{{ user.fullnameEng }}</h3>
            <p>{{ user.university }}</p>
            <p>{{ user.qt1 }}</p>
          <router-link :to="{ name: 'Profile', params: { id: user._id } }">
              <button class="button">
                    View
              </button>
          </router-link>
        </div>
      </div>
  </div>
</div>
</template>

<script>
    import { mapGetters } from 'vuex'
    export default {
        props: [
            'users',
            'university',
            'showUsers'
        ],
        data: () => ({
            batchs:[
                {batch:"Batch 1 (1992)", id:"b1"},
                {batch:"Batch 2 (1993)", id:"b2"},
                {batch:"Batch 3 (1994)", id:"b3"},
                {batch:"Batch 4 (1995)", id:"b4"},
                {batch:"Batch 5 (1996)", id:"b5"},
                {batch:"Batch 6 (1997)", id:"b6"}
            ],
        }),
        computed: {
            ...mapGetters([
                'auth'
            ]),
            batch () {
                  const batch = (user) =>
                      this.batchs.filter(batch=>{
                         return batch.id === user
                      })
                return batch
            },
        }

    }
</script>

